"use client";

import React, { useEffect, useState } from "react";
import {
  Box,
  Flex,
  Text,
  Avatar,
  Input,
  HStack,
  VStack,
  Textarea,
  Button,
  Tag,
  TagLabel,
  IconButton,
  useToast,
} from "@chakra-ui/react";
import { FaGlobe, FaInstagram, FaSnapchatGhost, FaTiktok } from "react-icons/fa";
import placeholderAvatar from "../../data/image/no-avatar.png";
import useColorModeStyles from "../../utils/useColorModeStyles";

interface UserData {
  profile_image: string;
  firstName: string;
  lastName: string;
  email: string;
  location: string;
  age: number;
  bio: string;
  views: number;
  likes: number;
  points: number;
  interests: string[];
  instagram?: string;
  snapchat?: string;
  tiktok?: string;
}

const MainSettings = () => {
  const { bg, textColor, borderColor, navBgColor } = useColorModeStyles();
  const toast = useToast();

  const [userData, setUserData] = useState<UserData>({
    profile_image: placeholderAvatar.src,
    firstName: "",
    lastName: "",
    email: "",
    location: "",
    age: 0,
    bio: "",
    views: 0,
    likes: 0,
    points: 0,
    interests: [],
    instagram: "",
    snapchat: "",
    tiktok: "",
  });

  const [imagePreview, setImagePreview] = useState<string>(
    placeholderAvatar.src
  );

  useEffect(() => {
    const fetchUserData = async () => {
      try {
        const response = await fetch("/api/user/profile");
        const data = await response.json();
        setUserData(data);
        setImagePreview(data.profile_image || placeholderAvatar.src);
      } catch (error) {
        console.error("Failed to fetch user data:", error);
      }
    };

    fetchUserData();
  }, []);

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target;
    setUserData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleInterestsChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;
    setUserData((prev) => ({
      ...prev,
      interests: value.split(",").map((item) => item.trim()),
    }));
  };

  const handleImageChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      const reader = new FileReader();
      reader.onloadend = () => {
        setImagePreview(reader.result as string);
        setUserData((prev) => ({
          ...prev,
          profile_image: reader.result as string,
        }));
      };
      reader.readAsDataURL(file);
    }
  };

  const handleSaveChanges = async () => {
    try {
      const response = await fetch("/api/user/profile", {
        method: "PUT",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(userData),
      });

      if (!response.ok) {
        throw new Error("Failed to update profile");
      }

      toast({
        title: "Profile Updated",
        description: "Your profile has been successfully updated.",
        status: "success",
        duration: 3000,
        isClosable: true,
      });
    } catch (error) {
      console.error("Failed to update profile:", error);
      toast({
        title: "Update Failed",
        description: "An error occurred while updating your profile.",
        status: "error",
        duration: 3000,
        isClosable: true,
      });
    }
  };

  return (
    <Flex justify="center" align="center" py={5} px={5}>
      <Box
        maxW="lg"
        w="full"
        bg={bg}
        borderRadius="lg"
        p={8}
        boxShadow="lg"
        borderColor={borderColor}
        borderWidth="1px"
      >
        {/* Avatar Upload */}
        <Flex justify="center" mb={4}>
          <Avatar size="2xl" src={imagePreview} />
        </Flex>
        <Input type="file" accept="image/*" onChange={handleImageChange} mb={4} />

        {/* Personal Information */}
        <VStack spacing={4} align="start">
          <Input
            placeholder="First Name"
            name="firstName"
            value={userData.firstName}
            onChange={handleInputChange}
          />
          <Input
            placeholder="Last Name"
            name="lastName"
            value={userData.lastName}
            onChange={handleInputChange}
          />
          <Input
            placeholder="Email"
            name="email"
            type="email"
            value={userData.email}
            onChange={handleInputChange}
          />
          <Input
            placeholder="Location"
            name="location"
            value={userData.location}
            onChange={handleInputChange}
          />
          <Input
            placeholder="Age"
            name="age"
            type="number"
            value={userData.age}
            onChange={handleInputChange}
          />
          <Textarea
            placeholder="Bio"
            name="bio"
            value={userData.bio}
            onChange={handleInputChange}
          />
          <Input
            placeholder="Interests (comma-separated)"
            value={userData.interests.join(", ")}
            onChange={handleInterestsChange}
          />
        </VStack>

        {/* Social Media Links */}
        <Text mt={4} fontSize="lg" fontWeight="bold">
          Social Media Links
        </Text>
        <VStack spacing={2} align="start">
          <Input
            placeholder="Instagram URL"
            name="instagram"
            value={userData.instagram || ""}
            onChange={handleInputChange}
          />
          <Input
            placeholder="Snapchat URL"
            name="snapchat"
            value={userData.snapchat || ""}
            onChange={handleInputChange}
          />
          <Input
            placeholder="TikTok URL"
            name="tiktok"
            value={userData.tiktok || ""}
            onChange={handleInputChange}
          />
        </VStack>

        {/* Save Button */}
        <Button
          colorScheme="teal"
          mt={6}
          w="full"
          onClick={handleSaveChanges}
        >
          Save Changes
        </Button>
      </Box>
    </Flex>
  );
};

export default MainSettings;
